const app = getApp()

export default {
	data() {
		return {
			videoData: {},
			muted: false,
			play: true
		}
	},
	methods: {
		toggleMuted() {
			this.muted = !this.muted
		},
		handleDown() {

			uni.showLoading({
				title: "加载中"
			})
			
			// console.log(this.videoData.viedo)
			uni.downloadFile({
				url: this.videoData.video,
				success(res) {
					// console.log(res.tempFilePath)
					uni.saveVideoToPhotosAlbum({
						filePath: res.tempFilePath,
						success() {
							uni.showToast({
								title: "下载成功",
								icon: "none"
							})
						},
						fail() {
							uni.showToast({
								title: "取消下载",
								icon: "none"
							})
						}
					})
				},
				complete() {
					uni.hideLoading()
				}
			})
		},
		startPlay() {
				this.videoContext.play()
				this.play = false
		},
		cancelPlay() {
				this.videoContext.pause()
				this.play = true
		}
	},
	onLoad() {
		this.videoData = app.globalData.viedo
	},
	onReady() {
	    this.videoContext = uni.createVideoContext('myVideo')
	}
}
